.form--container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 10rem;
    margin-bottom: 4rem;

    .form--header__row {
      text-align: center;
      @extend .form--row;

      .form--title {
        font-size: 3rem;
        font-weight: 600;
        letter-spacing: 0.2rem;
        line-height: 4.6rem;
        color: #a2c3aa;
        margin-bottom: 1rem;
      }
      .form--title__description {
        font-size: 1.4rem;
        font-weight: 400;
        letter-spacing: 0.2rem;
        line-height: 2.6rem;
        color: #a2c3aa;
        margin-bottom: 4rem;
      }
    }

    .form--row {
      width: 45rem;
      // align-self: flex-start;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-bottom: 2rem;

      .form--label {
        font-size: 1.5rem;
        font-weight: 400;
        letter-spacing: 0.2rem;
        line-height: 2.5rem;
        color: #a2c3aa;
        margin-bottom: 1rem;
      }
      .form--input {
        width: 100%;
        //   height: 6.1rem;
        padding: 1.2rem;
        border: none;
        border-radius: 1rem;
        box-shadow: 0 0.1rem 0.6rem rgba(#168c34, 0.25);
        &:focus {
          outline-color: #a2c3aa;
        }
      }
    }

    .form--row__button {
      // margin-top: 3rem;
      width: 45rem;
      height: 6.1rem;
      // width: 21rem;
      // height: 6.1rem;
      display: flex;

      .form--button {
        justify-self: flex-end;
        align-self: flex-end;
        width: 21.1rem;
        //   height: 6.1rem;
        padding: 1.2rem 4.4rem;
        font-size: 1.5rem;
        font-weight: 400;
        line-height: 2.5rem;
        letter-spacing: 0.2rem;
        color: #a2c3aa;
        border: none;
        background: #fff;
        border-radius: 1.5rem;
        box-shadow: 0 0.1rem 0.6rem rgba(#168c34, 0.25);
        cursor: pointer;
        transition: all 0.3s ease-out;
        &:hover {
          // transform: scale(1.03);
          background-color: #e8f6ec;
          box-shadow: 0 0.1rem 0.6rem rgba(#168c34, 0.4);
          transition: all 0.3s ease-out;
        }
      }
    }
  }

  .form--row {
    width: 45rem;
    // align-self: flex-start;
    display: flex;
    // flex-direction: column;

    .form--new-admin__title {
      font-size: 1.5rem;
      font-weight: 400;
      letter-spacing: 0.2rem;
      line-height: 2.5rem;
      color: #a2c3aa;
      margin-bottom: 1rem;
      margin-right: 2rem;
    }

    .form--new-admin__link {
      text-decoration: none;
      font-size: 1.5rem;
      font-weight: 400;
      letter-spacing: 0.2rem;
      line-height: 2.5rem;
      color: $green-color-dark3;
      transition: 0.2s ease-out;

      &:hover {
        transition: 0.2s ease-out;
        color: lighten($green-color-dark3, 15%);
      }
    }
  }
}
