.dashboard--layout {
  width: 100%;
  display: flex;
  padding-top: 5rem;
  // position: relative;
}
// .dashboard--content {
//   max-width: 1366px;
//   margin: 0 auto;
// }

.dashboard--container {
  padding-top: 5rem;
  padding-left: 4rem;
  display: flex;
  max-width: 1366px;

  .dashboard--cards {
    padding-right: 3rem;

    .dashboard--cards__wrapper {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      //   grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
      grid-gap: 3rem;

      .dashboard--card__small {
        width: 25rem;
        height: 12rem;
        border-radius: 1.5rem;
        box-shadow: 0 0.1rem 0.8rem $dashboard-shadow;
        background-color: #fff;
      }
    }
  }

  .dashboard--cards__header {
    display: flex;
    flex-direction: column;
    padding-bottom: 3rem;

    .dashboard--cards__title {
      font-size: 1.8rem;
      font-weight: 400;
      line-height: 2.7rem;
      letter-spacing: 0.9px;
      color: $dashboard-title;
      text-transform: uppercase;
      padding-bottom: 1.35rem;
    }
    .dashboard--cards__title-line {
      width: 5rem;
      height: 0.3rem;
      background-color: $dashboard-title-line;
    }
  }

  .dashboard--card-right {
    .dashboard--card__medium {
      width: 44.6rem;
      height: 27rem;
      border-radius: 1.5rem;
      box-shadow: 0 0.1rem 0.8rem $dashboard-shadow;
    }
  }
}

@media screen and (max-width: 1900px) {
  .dashboard--container {
    width: 70vw;
    margin: 0 auto;
  }
  .dashboard--container .dashboard--cards .dashboard--cards__wrapper {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;
  }
}
@media screen and (max-width: 900px) {
  .dashboard--container {
    width: 70vw;
    margin: 0 auto;
  }
  .dashboard--container .dashboard--cards .dashboard--cards__wrapper {
    grid-template-columns: repeat(1, 1fr);
    // grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 3rem;
  }
}
