.dashboard--navbar {
  max-width: 23rem;
  min-width: 23rem;
  height: 80vh;
  max-height: 83rem;
  background-color: #fff;
  border-radius: 1.5rem;
  box-shadow: 0 0.1rem 0.8rem $dashboard-shadow;
  margin-top: 5rem;
  margin-bottom: 5rem;
  padding: 3rem 1rem 3rem 3rem;
  position: absolute;
  left: 47px;

  .dashboard--navbar__items {
    list-style: none;
    .dashboard--navbar__item {
      // display: flex;
      // align-items: center;
      padding-bottom: 3rem;
      .dashboard--navbar__icon {
        width: 4.5rem;
        height: 4.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $dashboard-box;
        border-radius: 1rem;
        box-shadow: 0 0.1rem 0.6rem rgba($dashboard-shadow2, 0.5);
        margin-right: 2rem;

        &:hover {
          background-color: $dashboard-primary;
          box-shadow: 0 0.1rem 0.6rem $dashboard-shadow-active;
        }

        .active {
          background-color: $dashboard-primary;
          box-shadow: 0 0.1rem 0.6rem $dashboard-shadow-active;
        }

        img {
          width: 2rem;
        }
      }
      .dashboard--navbar__link {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        font-weight: 400;
        line-height: 2.3rem;
        letter-spacing: 0.75px;
        text-decoration: none;
        color: $dashboard-secondary;

        &:hover {
          color: $dashboard-primary;
        }

        .active {
          color: $dashboard-primary;
        }
      }
    }
  }
}
